import React           from 'react';
import InformationBox  from '../../components/Profiles/InformationBox';
import {Table} from 'react-bootstrap';
import logo   from '../../assets/img/Profiles/gem.png'


const InformationProfile = (props) => {
   return (
       <div className="o-container">
        <div className = "u-medium-padding u-medium-padding-top u-medium-margin-bottom">
            <InformationBox />
            {/* <td className="c-profile_table">
                <button type="button" className="btn btn-primary c-button__information">امروز</button>
            </td> */}
        </div>
        <div className="u-horizontal-center">
        <Table responsive borderless> 
            
            <tbody className="u-horizontal-center">
                {
                    props.data

                    ? props.data.map(function(item){
                       return(
                        <tr>               
                            <td className="u-small-padding-vertical"><img src={item.user && item.user.avatar ? item.user.avatar : null}></img></td>
                            <td className="u-small-padding-vertical">{item.user && item.user.schoolName ? item.user.schoolName : null}</td>
                            <td className="u-small-padding-vertical">{item.user && item.user.firstName ? item.user.firstName: null} {item.user && item.user.lastName ? item.user.lastName: null}</td>
                            <td className="u-small-padding-vertical">{item.user && item.user.city ? item.user.city : null}</td>
                            <td className="u-small-padding-vertical">{item.user && item.user.educationLevel ? item.user.educationLevel : null}</td>
                            <td className="u-small-padding-vertical"> 
                                <img width="30" height="30" src={logo}></img>
                                {item.total}
                            </td>
                        </tr>
                       );

                    })
                    :null
                }
                
            </tbody>
        </Table>
    </div>
      </div>
   );
}

export default InformationProfile;

   